.secao-produto{
    width: 100%;
    text-align: center;
    padding: 100px 20px;
}

.colecao-card{
    margin: 50px 0px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
}

.card{
    background-color: #f6f6f6;
    width: calc(50% - 15px);
    padding: 20px 10px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.card h3{
    margin: 0;
    font-size: 32px;
}

.card p{
    margin: 0;
    font-weight: bold;
    font-size: 22px;
}

.card img{
    width: 80%;
    max-height: 150px;
    margin: 30px 0px;
}

.secao-produto ul{
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.secao-produto li{
    text-align: left;
    margin: 5px;
}

@media (max-width: 768px) {
    .secao-produto{
        padding: 60px 20px;
    }

    .card h3{
        font-size: 22px;
    }

    .card img{
        max-height: 100px;
    }

    .card p{
        font-size: 16px;
    }
}


@media (max-width: 550px) {
    .secao-produto{
        padding: auto 10px;
    }

    .card{
        width: 100%;
    }

    .secao-produto ul{
        align-items: flex-start;
    }
}