.secao-capa{
    background-image: url('/public/assets/capa.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    height: 80vh;
  }

.secao-capa div{
    height: 100%;
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.secao-capa p, .secao-capa h1{
    max-width: 500px;
    font-family: 'Anybody', cursive;
}

.secao-capa p{
    font-size: 36px;
    color: rgb(196, 196, 196);
}

.secao-capa h1{
    text-transform: uppercase;
    color: #fff;
    font-size: 60px;
    margin: 0;
    letter-spacing: 5px;
    line-height: 70px;
}

@media (max-width: 768px) {
    .secao-capa h1{
        font-size: 40px;
        max-width: 350px;
    }

    .secao-capa p{
        font-size: 20px;
    }
}

@media (max-width: 550px) {
    .secao-capa{
        background-position: 89% center;
    }

    .secao-capa div{
        background-color: rgb(0, 0, 0, 0.6);
    }
}